import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Refresher } from 'ionic-angular';
import { WeatherServiceProvider } from '../../providers/weather-service/weather-service';
import { Geolocation } from '@ionic-native/geolocation';

/**
 * Generated class for the WeatherPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-weather',
    templateUrl: 'weather.html',
})
export class WeatherPage {
    theWeather: any = {};
    currentData: any = {};
    daily: any = {};
    loader: LoadingController;
    refresher: Refresher;

    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public weatherService: WeatherServiceProvider,
        public loadingCtrl: LoadingController,
        public geolocation: Geolocation) {
        let loader = this.loadingCtrl.create({
            content: "Loading weather dataa ...",
            duration: 1000
        });
        loader.present();
        this.geolocation.getCurrentPosition().then(pos => {
            console.log('lat: ' + pos.coords.latitude + ', lon: ' + pos.coords.longitude);
        });
        this.weatherService.getWeather().then(theResult => {
            this.theWeather = theResult;
            this.currentData = this.theWeather.currently;
            this.daily = this.theWeather.daily;
        });
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad WeatherPage');
    }

    doRefresh(refresher) {
        setTimeout(() => { refresher.complete(); }, 2000);
    }
}



